<template>
    <div class="tab5_color">
    <Bank />
        <div style="height: 10px"> </div>
        <div @click="uuu('BC000')" class="color_ BC00"></div>
        <div @click="uuu('BC001')" class="color_ BC01"></div>
        <div @click="uuu('BC002')" class="color_ BC02"></div>
        <div @click="uuu('BC003')" class="color_ BC03"></div>
        <div @click="uuu('BC004')" class="color_ BC04"></div>
        <div @click="uuu('BC005')" class="color_ BC05"></div>
        <div @click="uuu('BC006')" class="color_ BC06"></div>
        <div @click="uuu('BC007')" class="color_ BC07"></div>
        <div @click="uuu('BC008')" class="color_ BC08"></div>
        <div @click="uuu('BC009')" class="color_ BC09"></div>
    </div>
</template>
<script>
import Bank from '@/components/NavBar/bank.vue'
export default {
    components:{
        Bank
    },
    data() {
        return {
            radio: '',
            colorList:['BC000']
        }
    },
    computed: {
        getBackgroundImage() {
        return this.$store.getters.getBackgroundImage;
        },
    },
    methods: {
            uuu( color ){
                this.$store.commit("newBackgroundImage", color);
                document.body.className = '';
                document.body.classList.add(`${this.getBackgroundImage}`);
            }
        }
}
</script>

<style lang="scss">
    .tab5_color{
    .color_{
        height: 50px;
        width: 100%;
        margin-bottom: 10px;
        border: solid 30px rgba(0,0,0,0.3);
        box-sizing: border-box;
    }
    .color_:active{
           opacity: 0;
    }
    .BC00{ background-image: url('../../../assets/imgs/0.png'); }
